<template>
  <div class="switch-container">
    <span class="switch-label">RUB</span>
    <label class="switch">
      <input type="checkbox" :checked="isUSD" @click="toggleCurrency">
      <span class="slider round"></span>
    </label>
    <span class="switch-label">USD</span>
  </div>

</template>

<script>
import {mapMutations,} from 'vuex';

export default {
  name: "CurrencySwitch",
  computed: {
    isUSD() {
      return this.$store.state.isUSD;
    }
  },
  methods: {
    ...mapMutations(['toggleCurrency'])
  }
};
</script>

